import { BrowserRouter as Router, Link, Switch, Route } from 'react-router-dom';
import List from './components/List';
import vobs from './data';

function App() {

  function url(path) {
    return process.env.PUBLIC_URL + "/" + path;
  } 

  return (
    <Router>
      <nav>
        <div className="links">
          <Link to={url("static")}>Voby statyczne</Link>
          <Link to={url("nature")}>Voby natury</Link>
          <Link to={url("effects")}>Efekty wizualne</Link>
          <Link to={url("sounds")}>Efekty dźwiękowe</Link>
          <Link to={url("all")}>Wszystkie voby</Link>
        </div>
      </nav>
      <Switch>
        <Route path={url("static")}>
          <List data={vobs.static} />
        </Route>
        <Route path={url("nature")}>
          <List data={vobs.nature} />
        </Route>
        <Route path={url("effects")}>
          <List data={vobs.effects} />
        </Route>
        <Route path={url("sounds")}>
          <List data={vobs.sounds} />
        </Route>
        <Route path={url("all")}>
          <List data={vobs.all} />
        </Route>
      </Switch>
    </Router>
  );
}

export default App;
